<template>
    <div>
        <Table border :columns="cols" :data="rows">
            <template slot-scope="{row}" slot="operation">
                <div>
                    <Dropdown transfer @on-click="clickOperation($event, row)">
                        <Button type="primary">操作
                            <Icon type="ios-arrow-down"></Icon>
                        </Button>
                        <DropdownMenu slot="list">
                            <DropdownItem v-if="row.status" name="down">下架</DropdownItem>
                            <DropdownItem v-if="!row.status" name="up">上架</DropdownItem>
                            <DropdownItem name="addplan">增加销售计划</DropdownItem>
                            <DropdownItem name="plan">销售计划</DropdownItem>
                            <DropdownItem name="delete" v-if="!row.status">删除</DropdownItem>
                            <DropdownItem name="modify" v-if="!row.status">修改</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </div>
            </template>
        </Table>
    </div>
</template>
<script>
import TableColRender from '@/mixins/table-col-render';
export default {
    mixins: [TableColRender],
    props: {
        rows: Array
    },
    data () {
        return {
            cols: [
                {
                    title: '票据名称',
                    key: 'name'
                },
                {
                    title: '市场价',
                    key: 'marketPrice',
                    render: this.renderMoney
                },
                {
                    title: '票种',
                    key: 'ticketDict',
                    render: (h, params) => (
                        <span>{params.row.ticketDict === 3001 ? '散客票' : '团队票'}</span>
                    )
                },
                {
                    title: '状态',
                    key: 'status',
                    render: this.renderStatus
                },
                {
                    title: '操作时间',
                    key: 'operateTime',
                    render: this.renderDate
                },
                {
                    title: '操作',
                    slot: 'operation'
                }
            ]
        };
    },
    methods: {
        async clickOperation (event, row) {
            if (event === 'up') {
                const result = await this.post('/api/product/scenic/sub/upordown', {
                    id: row.id,
                    status: true
                });
                if (result.success) {
                    this.$Notice.success({ title: '上架成功' });
                    this.$emit('on-query');
                }
            } else if (event === 'down') {
                const result = await this.post('/api/product/scenic/sub/upordown', {
                    id: row.id,
                    status: false
                });
                if (result.success) {
                    this.$Notice.success({ title: '下架成功' });
                    this.$emit('on-query');
                }
            } else if (event === 'addplan') {
                this.$router.push({
                    name: 'TicketPlan',
                    params: {
                        productSubId: row.id,
                        productId: row.productId,
                        productSubName: row.name
                    }
                });
            } else if (event === 'plan') {
                this.$router.push({
                    name: 'PlanList',
                    params: { productSubId: row.id, productSubName: row.name }
                });
            } else if (event === 'delete') {
                const result = await this.post(
                    `/api/product/scenic/sub/delete/${row.id}`
                );
                if (result.success) {
                    this.$Notice.success({ title: '删除成功' });
                    this.$emit('on-query');
                }
            } else if (event === 'modify') {
                this.$router.push({
                    name: 'TicketUpdate',
                    params: { productSub: row }
                });
            }
        }
    }
};
</script>
