<template>
    <div>
        <Row>
            <Button type="primary" @click="add">
                <Icon type="plus"></Icon>新增景点
            </Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">主产品名称：</label>
            <Input placeholder clearable v-model="table.query.name" style="width: 200px"/>
            <label style="margin-left:10px;">子产品名称：</label>
            <Input placeholder clearable v-model="table.query.subName" style="width: 200px"/>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>

        <Row class="margin-top-10">
            <Table :columns="table.cols" :data="table.data"></Table>
            <i-col>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page
                            :total="table.total"
                            :show-sizer="true"
                            placement="top"
                            @on-page-size-change="changePageSize"
                            @on-change="changePage"
                        ></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal
            v-model="model"
            :mask-closable="false"
            :title="modelTitle"
            :loading="loadingBtn"
            @on-ok="addOrUpdate"
            @on-cancel="cancel"
        >
            <div style="margin-left=30px;">
                <Form ref="form" :model="vo" :rules="ruleValidate" :label-width="80">
                    <FormItem label="产品名" prop="name">
                        <Input v-model="vo.name" placeholder="请填写产品名，一旦填写不可修改"/>
                    </FormItem>
                    <FormItem label="省市县" prop="pcastr">
                        <al-cascader ref="pcastr" v-model="pcastr" :level="2"/>
                    </FormItem>
                    <FormItem label="景点级别" prop="level">
                        <Select v-model="vo.level" placeholder="选取景点级别">
                            <Option :value="5">AAAAA</Option>
                            <Option :value="4">AAAA</Option>
                            <Option :value="3">AAA</Option>
                            <Option :value="2">AA</Option>
                            <Option :value="1">A</Option>
                            <Option :value="0">无级别</Option>
                        </Select>
                    </FormItem>
                    <FormItem label="详细地址" prop="adress">
                        <Input v-model="vo.adress" placeholder="景点详细地址"/>
                    </FormItem>
                    <FormItem label="服务电话" prop="tel">
                        <Input v-model="vo.tel" placeholder="例如：0731-5264251"/>
                    </FormItem>
                    <FormItem label="营业时间" prop="businessTime">
                        <Input v-model="vo.business_time" placeholder="例如：8：30-17:30"/>
                    </FormItem>
                    <FormItem label="交通路线" prop="transitLine">
                        <Input v-model="vo.transitLine" placeholder="例如：地铁2号线——>公交"/>
                    </FormItem>
                    <FormItem label="景点介绍" prop="blurb">
                        <Input
                            v-model="vo.blurb"
                            type="textarea"
                            :autosize="{minRows: 2,maxRows:8}"
                            placeholder="描写景点特征"
                        />
                    </FormItem>
                    <FormItem label="图片" prop="imgs">
                        <OssUpload :files="imgs" :size="5" :format="['jpg','jpeg','png']" accept="image/*"/>
                    </FormItem>
                </Form>
            </div>
        </Modal>
    </div>
</template>

<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import ExpandRow from './table-expland.vue';
import { validateTelphone } from '@/libs/tools';
import OssUpload from '@/components/upload';
import bus from '@/assets/eventBus';
export default {
    name: 'Scenery',
    components: { ExpandRow, OssUpload },
    mixins: [TableColRender, TableDataView, ModelView],
    data () {
        return {
            addUrl: '/api/product/scenic/update',
            updateUrl: '/api/product/scenic/update',
            removeUrl: '/api/product/scenic/delete',
            table: {
                url: '/api/product/scenic/list',
                query: {
                    name: null,
                    subName: null
                },
                cols: [
                    {
                        type: 'expand',
                        width: 50,
                        render: (h, params) => (
                            <ExpandRow rows={params.row.subs} onOn-query={this.doQuery} />
                        )
                    },
                    {
                        title: '主产品名',
                        key: 'name'
                    },
                    {
                        title: '操作',
                        width: 350,
                        align: 'center',
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.addTicket(params.row),
                                    text: '增票据'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.update(params.row),
                                    text: '修改'
                                },
                                {
                                    type: 'delete',
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.remove(params.row),

                                    text: '删除'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.showDetail(params.row),
                                    text: '详情'
                                }
                            ])
                    }
                ],
                data: []
            },
            vo: {
                id: null,
                name: null,
                province: null,
                area: null,
                tel: null,
                level: null,
                adress: null,
                transitLine: null,
                map: null,
                blurb: null,
                imgs: null,
                pcastr: null
            },
            imgs: [],
            pcastr: [],
            uploadList: [],
            ruleValidate: {
                name: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { trigger: 'blur', validator: null }
                ],
                area: [{ required: true, message: '不能为空' }],
                tel: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { trigger: 'blur', validator: validateTelphone }
                ],
                pcastr: [{ required: true, message: '不能为空', trigger: 'blur' }],
                level: [{ required: true, message: '不能为空' }]
            }
        };
    },
    computed: {
        generateAddOrUpdate () {
            const last = this.pcastr[this.pcastr.length - 1] || {};
            return {
                ...this.vo,
                area: last.code,
                pcastr: this.pcastr.map(item => item.name).join(''),
                imgs: JSON.stringify(this.imgs.map(item => ({ link: item.name })))
            };
        }
    },
    watch: {
        pcastr (val, oldVal) {
            this.vo.pcastr = val.map(item => item.name).join('');
        }
    },
    mounted () {
        bus.$on('scenery-update', this.doQuery);
    },
    methods: {
        showDetail (id) {
            console.log(id);
        },
        processQueryData (data) {
            return data.map(item => ({ ...item, _expanded: true }));
        },
        addTicket (item) {
            this.$router.push({
                name: 'TicketAdd',
                params: { productId: item.id, productName: item.name }
            });
        },
        add () {
            this.$super(ModelView).add();
            this.imgs = [];
            this.vo.area = null;
            this.$nextTick(() => {
                this.$refs.pcastr.$children[0].clearSelect();
            });
        },
        async update (item) {
            this.$super(ModelView).update(item);
            this.setByItem(item);
        },
        setByItem (item) {
            if (item.area) {
                this.setAddress('pcastr', item.area);
            }
            if (item.imgs) {
                const arr = JSON.parse(item.imgs);
                this.imgs = arr.map(item => ({ name: item.link, status: true }));
            }
        },
        setAddress (name, val) {
            const temp = val.toString();
            this[name] = [
                temp.substring(0, 2).padEnd(6, '0'),
                temp.substring(0, 4).padEnd(6, '0'),
                temp
            ];
        },
        afterRemove (success, item) {
            console.log(123);

            this.$nextTick(() => this.loadingBtn = false);
            console.log(this.loadingBtn);
        }
    }
};
</script>

<style>
td.ivu-table-expanded-cell {
    padding: 0;
    background: #f8f8f9;
}
</style>
